.product-card {
  background: white;
  border-radius: 12px;
  padding: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;
  text-align: center;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.product-details {
  margin-top: 12px;
}

.product-price {
  color: #0071dc;
  font-size: 1.2rem;
  font-weight: bold;
}

.product-desc {
  font-size: 0.9rem;
  color: #555;
  margin: 8px 0;
}

.btn-add {
  background: #0071dc;
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.btn-add:hover {
  background: #005bb5;
}
