.page-container {
  padding: 20px;
}

h2 {
  margin-bottom: 20px;
  color: #0a192f;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.loading {
  text-align: center;
  font-size: 1.2rem;
  margin-top: 50px;
}

.orders-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.order-card {
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.order-card h3 {
  margin-bottom: 8px;
  color: #0071dc;
}

.order-card ul {
  margin: 10px 0;
  padding-left: 20px;
}

.order-total {
  margin-top: 10px;
  font-size: 1.2rem;
  color: #28a745;
}
