.navbar {
  background: #0071dc;
  color: #fff;
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.navbar-logo {
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 25px;
}

.navbar-links li {
  position: relative;
}

.navbar-links a {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
  padding: 6px 10px;
  border-radius: 6px;
  transition: background 0.3s ease;
}

.navbar-links a:hover {
  background: rgba(255, 255, 255, 0.2);
}

.navbar-links .active {
  background: #005bb5;
  font-weight: bold;
}

.cart-badge {
  background: #ff4d4d;
  color: #fff;
  font-size: 0.8rem;
  padding: 2px 7px;
  border-radius: 12px;
  margin-left: 5px;
}
