.artisans-container {
    padding: 30px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  
  .artisans-container h2 {
    text-align: center;
    margin-bottom: 30px;
    color: #333;
  }
  
  .artisan-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 25px;
  }
  
  .artisan-card {
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    transition: transform 0.2s ease-in-out;
  }
  
  .artisan-card:hover {
    transform: translateY(-5px);
  }
  
  .artisan-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 15px;
  }
  
  .artisan-tagline {
    font-style: italic;
    color: #555;
    margin: 10px 0;
  }
  
  .artisan-products {
    list-style: none;
    padding: 0;
    margin-top: 10px;
    font-size: 0.95rem;
    color: #444;
  }
  